body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav_title {
    color: #FFF;
    font-family: 'Sarina', cursive;
    font-size: 3rem;
}

.nav_subtitle {
    color: #FFF;
    font-size: 2rem;
    font-family: 'Century Gothic', cursive;
}

.nav-item a {
    color: #FFF !important;
}

.navbar {
    margin-bottom: 0 !important;
}

.dropdown-menu a {
    color: #000 !important;
    padding-top: 7px;
    padding-bottom: 7px;
}

.dropdown-menu .nav-item {
    padding-left: 11px;
}

.dropdown-menu .nav-item a {
    padding-left: 7px;
}
/* 
@media (max-width: 1180px) {
    .nav_title {
        font-size: 2.5rem;
    }

    .nav_subtitle {
        font-size: 1.5rem;
    }
} */

@media (max-width: 993px) {
    a.nav_subtitle.nav-link.disabled {
        font-size: 1.3rem !important;
    }
}

@media (max-width: 991px) {
    a.nav_subtitle.nav-link.disabled {
        display: none !important;
    }
}

@media (max-width: 708px) {
    .nav_title {
        font-size: 2.5rem;
    }

    .nav_subtitle {
        font-size: 1.7rem;
    }
}

@media (max-width: 613px) {
    .nav_title {
        font-size: 2.2rem;
    }

    .nav_subtitle {
        font-size: 1.5rem;
    }
}

@media (max-width: 552px) {
    .nav_title {
        font-size: 2rem;
    }

    .nav_subtitle {
        font-size: 1.3rem;
    }
}

@media (max-width: 552px) {
    .nav_title, .nav_subtitle {
        display: none;
    }
}

@media (max-width: 375px) {
    .btnPosition {
        position: absolute;
        right: 15px;
    }
}
.background_img {
    background: -webkit-linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(/static/media/broken_record.a1428577.jpg) no-repeat;
    background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(/static/media/broken_record.a1428577.jpg) no-repeat;
    height: 83vh;
    background-position: center center;
    background-size: cover;
}

.content {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    text-align: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.content h1 {
    background: -webkit-linear-gradient(right, #ffbc00, #f77110);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: bold;
    font-size: 5.5em;
    margin: 0;
}

.content h2 {
    font-size: 3.5em;
    margin-bottom: 4rem;
    margin-top: 0;
    color: #feb403;
    font-weight: bold;
    letter-spacing: 2px;
}

.content p {
    font-size: 1.5em;
    color: #FFF;
    margin-bottom: 20px;
}

.spotify_btn {
    padding: 5px 7px;
    width: 27%;
    margin: 0 auto;
    background-color: antiquewhite;
}

@media (max-width: 708px) {
    .content h1 {
        font-size: 3em;
    }

    .content h2 {
        font-size: 2.5em;
    }

    .content p {
        font-size: 1.1em;
    }

    .spotify_btn {
        width: 43%;
    }
}

@media (max-width: 552px) {
    .content h1 {
        font-size: 3em;
    }

    .content h2 {
        font-size: 2em;
        margin-bottom: 3rem;
    }

    .content p {
        font-size: 1em;
        margin-bottom: 10px;
    }

    .spotify_btn {
        font-size: 0.8em;
    }
}

@media (max-width: 375px) {
    .content h1 {
        font-size: 2.5em;
    }

    .content h2 {
        font-size: 1.5em;
        margin-bottom: 2rem;
        margin-top: 1rem;
    }

    .content p {
        font-size: 0.8em;
        margin-bottom: 7px;
    }

    .spotify_btn {
        font-size: 0.6em;
    }
}
.listed_item {
    font-family: 'sans-serif';
    margin: '1.5em';
    margin-top: '0';
}
.collection {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 30px;
    margin: 1rem;
    padding: 0
}

.collectionPiece {
    list-style-type: none;
    margin: 0 auto;
}

.collection_container {
    max-height: 25rem;
    overflow-y: scroll;
}
.playlistGrid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 20px;
    padding: 0;
    margin-bottom: 20px;
}

.imgContainer {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}

.shareBtn {
    position: absolute;
}
