.friend-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}

.inner-borders {
    border: 1px solid #000;
}
@media (max-width: 708px) {
    .footerList {
        font-size: 0.8em;
        padding: 0;
    }
}

@media (max-width: 375px) {
    .footerList {
        font-size: 0.6em;
        padding: 0;
    }
}
.pagetitle {
    text-decoration: underline; 
    text-align: center;
    margin: 1.5rem
}
.img-container {
    display: grid;
    grid-template-columns: repeat(4, 25%);
    grid-gap: 10px;
    margin-top: 1rem;
    padding-right: 25px;  
}

.grid-container .container {
    background-color: #FFF;
    border-radius: 7px;
}

#img-wrapper {
    width: 25%;
}

.img-container li img {
    width: 100%;
    height: auto;
    object-fit: contain;
}

#img-preview {
    width: 100%;
    height: auto;
}
