  font-family: 'Century Gothic', cursive;
  background-color: #DDD;
}

.flex-box {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

label {
  margin-bottom: 0 !important;
}
